import "bootstrap/dist/css/bootstrap.min.css";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { fetchImages } from "../redux/Actions";
import "../styles/searchStyles.css";
import SearchIcon from "@material-ui/icons/Search";
import fetchData from "../services/fetchData";
import ImageComponent from "./imageComponent";
import { LaptopWindows } from "@material-ui/icons";

const SearchEngine = ({ fetchImages, imageData }) => {
  console.log(imageData, "hi");
  let query = "";
  const updateQuery = (e) => {
    query = e.target.value;
  };
  const getResults = (e) => {
    e.preventDefault();
    console.log("started");
    fetchImages(query);
  };
  const [limit, setLimit] = useState(10);

  const increment = () => {
    setLimit(limit + 10);
  };

  const appendResults = () => {
    if (imageData) {
      if (imageData.loading) {
        return <h1>Loading ....</h1>;
      } else if (imageData.images.results) {
        return (
          <div
            className="col-12 d-flex flex-row justify-content-center"
            style={{ flexWrap: "wrap" }}
          >
            {imageData.images.results.slice(0, limit).map((image) => (
              <ImageComponent src={image.urls.small} />
            ))}
          </div>
        );
      }
    } else {
      <></>;
    }
  };

  const loadMore = () => {
    console.log(imageData.images, "start");
    if (
      imageData.images.length !== 0 &&
      limit < imageData.images.results.length
    ) {
      return (
        <div class="col-12 d-flex flex-row justify-content-center">
          <button
            onClick={increment}
            class="btn btn-dark mb-5"
            style={{ width: 120, height: 40, fontSize: 13 }}
          >
            Load More
          </button>
        </div>
      );
    } else {
      return <></>;
    }
  };
  return (
    <div class="container">
      <div class="row">
        <div class="d-flex col-12 flex-row justify-content-center">
          <div class="">
            <form class="wiki-search-header" onSubmit={getResults}>
              <input
                onChange={updateQuery}
                placeholder="Search for photos"
                type="search"
                class="search-input shadow"
                style={{
                  width: "68vw",
                  zIndex: 1,
                  position: "relative",
                  fontSize: 12,
                  height: 45,
                  paddingLeft: 16,
                }}
                id="searchInput"
              />
              <button
                type="submit"
                style={{
                  borderStyle: "solid",
                  backgroundColor: "black",
                  width: 76,
                  height: 45,
                  marginLeft: 16,
                  display: "flex",
                  flexDirection: "column",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  style={{ color: "white" }}
                  fill="currentColor"
                  class="bi bi-search"
                  viewBox="0 0 16 16"
                >
                  <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                </svg>
              </button>
            </form>
          </div>
        </div>
        {appendResults()}
        {loadMore()}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    imageData: state.images,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    fetchImages: (query) => dispatch(fetchImages(query)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(SearchEngine);
